<template><section><html><head></head><body><h1>PolylineGraphics</h1>
<p>The <code v-pre="">vc-graphics-polyline</code> component is used to load a polyline which the first two positions define a line segment, and each additional position defines a line segment from the previous position. The segments can be linear connected points, great arcs, or clamped to terrain.</p>
<h2>Example</h2>
<h3>Load PolylineGraphics with <code v-pre="">vc-graphics-polyline</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity :polyline.sync="polyline1">
          <vc-graphics-polyline :positions="positions1" :material="material1" :width="5" :clamp-to-ground="false"></vc-graphics-polyline>
        </vc-entity>
        <vc-entity :polyline.sync="polyline2">
          <vc-graphics-polyline :positions="positions2" :material="material2" :width="10"></vc-graphics-polyline>
        </vc-entity>
        <vc-entity :polyline.sync="polyline3">
          <vc-graphics-polyline :positions="positions3" :material="material3" :width="10"></vc-graphics-polyline>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
