<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> A boolean Property specifying the visibility of the polyline.</td>
</tr>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre="">optional</code> A Property specifying the array of Cartesian3 positions that define the line strip. <strong>structure: [{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>width</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> A numeric Property specifying the width in pixels.</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> A numeric Property specifying the angular distance between each latitude and longitude if arcType is not ArcType.NONE.</td>
</tr>
<tr>
<td>material</td>
<td>Object|String|Array</td>
<td><code v-pre="">'WHITE'</code></td>
<td><code v-pre="">optional</code> A Property specifying the material used to draw the polyline.</td>
</tr>
<tr>
<td>depthFailMaterial</td>
<td>Object|String|Array</td>
<td></td>
<td><code v-pre="">optional</code> A property specifying the material used to draw the polyline when it is below the terrain.</td>
</tr>
<tr>
<td>arcType</td>
<td>Number</td>
<td><code v-pre="">1</code></td>
<td><code v-pre="">optional</code> The type of line the polyline segments must follow. <strong>NONE: 0, GEODESIC: 1, RHUMB: 2</strong></td>
</tr>
<tr>
<td>clampToGround</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> A boolean Property specifying whether the Polyline should be clamped to the ground.</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> An enum Property specifying whether the polyline casts or receives shadows from each light source. <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>classificationType</td>
<td>Number</td>
<td><code v-pre="">2</code></td>
<td><code v-pre="">optional</code> An enum Property specifying whether this polyline will classify terrain, 3D Tiles, or both when on the ground. <strong>TERRAIN: 0, CESIUM_3D_TILE: 1, BOTH: 2</strong></td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> A Property specifying at what distance from the camera that this polyline will be displayed. <strong>structure: { near: number, far: number }</strong></td>
</tr>
<tr>
<td>zIndex</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> A Property specifying the zIndex used for ordering ground geometry. Only has an effect if <code v-pre="">clampToGround</code> is true and polylines on terrain is supported.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/PolylineGraphics.html">PolylineGraphics</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>Triggers whenever a property or sub-property is changed or modified.</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          polyline1: {},
          positions1: [{ lng: 90, lat: 20, height: 10000 }, { lng: 120, lat: 20, height: 10000 }],
          material1: undefined,
          polyline2: {},
          positions2: [{ lng: 90, lat: 30, height: 10000 }, { lng: 120, lat: 30, height: 10000 }],
          material2: undefined,
          polyline3: {},
          positions3: [],
          material3: undefined
        }
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.material1 = 'red'
          this.material2 = {
            fabric: {
              type: 'PolylineGlow',
              uniforms: {
                glowPower: 0.2,
                color: 'blue'
              }
            }
          }
          this.material3 = new Cesium.PolylineArrowMaterialProperty(Cesium.Color.PURPLE)
          this.positions3 = [{ lng: 90, lat: 40, height: 10000 }, { lng: 120, lat: 40, height: 10000 }]
        }
      }
    }
  </script>